@import url(https://fonts.googleapis.com/css2?family=Luckiest+Guy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.main-page__content {
    display: flex;
    justify-content: space-between;
    padding: 20px;
}
.main-page__main-section {
    flex: 3 1;
}
.main-page__search-box {
    margin-bottom: 15px;
}

.main-page__favorites {
    margin-top: 70px;
    flex: 1 1;
}
.header {
    background: #496DDB;
    padding: 20px calc(50% - 500px) 10px;
}

.header__title {
    font-family: 'Luckiest Guy', cursive;
    color: #FFEEDB;
    font-size: 45px;
    margin-left: 40px;
}

.search-box__form {
    display: flex;
}

.search-box__form-label {
    display: inline-block;
    font-size: 12px;
    margin-right: 10px;
}

.search-box__form-input {
    box-sizing: border-box;
    display: block;
    margin-top: 5px;
    width: 300px;
    font-size: 14px;
    padding: 5px 10px;
    border: 1px solid rgba(0, 8, 7, .5);
}

.search-box__form-submit {
    margin-top: 17px;
    padding: 5px 10px;
    border: none;
    transition: all 0.2s linear;
    color: #FFEEDB;
    background-color: rgba(0, 8, 7);
}

.search-box__form-submit:hover:not(:disabled) {
    opacity: 0.8;
    cursor: pointer;
    transition: all 0.2s linear;
    background-color: #FFEEDB;
    color: rgba(0, 8, 7);
}

.search-box__form-submit:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}
.movie-item {
    padding: 4px;
    margin: 10px 10px 0 0px;
    border-radius: 5px;
    overflow: hidden;
    background-color: rgba(41, 105, 155, 0.38);
    display: flex;
}
.movie-item__info{
    width: 180px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 10px;
}
.movie-item__poster {
    width: 150px;
    height: 150px;
    object-fit: cover;
}

.movie-item__title {
    font-weight: 500;
    font-size: 21px;
}

.movie-item__add-button {
    border: none;
    background-color: #3d9d28;
    padding: 10px 15px;
    color: #FFEEDB;
    font-size: 14px;
    transition: all 0.2s linear;
}

.movie-item__add-button:hover {
    cursor: pointer;
    opacity: 0.9;
    transition: all 0.2s linear;
}

.movies {
    display: flex;
    flex-wrap: wrap;
}


.favorites {
    box-sizing: border-box;
    border: 1px solid rgba(0, 8, 7);
    padding: 30px 20px;
}

.favorites__name {
    box-sizing: border-box;
    width: 100%;
    font-size: 16px;
    padding: 5px 10px;
    border: 1px solid rgba(0, 8, 7, .5);
    margin-bottom: 24px;
}

.favorites__list {
    margin-bottom: 24px;
}
.favorites__list li{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2px;
    color: rgb(34, 0, 148);
    padding: 8px 0;
}

.favorites__list li button{
    flex-shrink: 0;
    border: none;
    cursor: pointer;
    background-color: transparent;
}
.favorites__list li button:hover {
    transition: .6s all;
    color: #ff0037;

}
.favorites__save {
    border: none;
    background-color: #496DDB;
    padding: 10px 15px;
    color: #FFEEDB;
    font-size: 14px;
    transition: all 0.2s linear;
    cursor: pointer;

}
.favorites__save:hover {
    opacity: 0.9;
    transition: all 0.2s linear;
}
.favorites__save:disabled{
pointer-events: none;
opacity: .3;
cursor:pointer;

 }
.list-page {
    padding: 42px calc(50% - 300px);
}

.list-page__title {
    font-size: 34px;
    margin-bottom: 26px;
}

.list-page ul li {
    font-size: 18px;
}
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
*{
	box-sizing: border-box !important;
}

/* Color palette https://coolors.co/ffeedb-e63946-6a8d73-000807-496ddb */

body, button, input {
    font-family: 'Roboto', 'Helvetica', sans-serif;
    color: #000807;
}


